

#home {
  height: 100vh;
  background: #1b242f;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#home h1 {
  margin: 10px;
  font-weight: normal;
  font-size: 2.2em;
}

#home h1 span {
  color: #cd661b;
}

#home button {
  margin-top: 20px;
  background: transparent;
  border: 3px solid #eee;
  border-radius: 3px;
  padding: 15px;
  font-size: 23px;
  color: #eee;
  cursor: pointer;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  outline: none;
}

@media screen and (max-width: 600px) {
  #home button {
    padding: 8px;
    font-size: 18px;
    border: 2px solid #eee;
  }
}

#home button:hover {
  background: #cd661b;
  border-color: #cd661b;
}

#home button i {
  margin-left: 5px;
  -webkit-transition: -webkit-transform 0.4s;
  transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s; 
}

#home button:hover i {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
nav {
  background: #1b242f;
  text-transform: uppercase;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 25px;
}

nav span {
  margin: 15px 20px;
}

nav span a {
  text-decoration: none;
  color: #eee;
  font-size: 1.05rem;
}

nav span a:hover {
  color: #cd661b;
}
#about {
  color: #444;
  background: #eee;
  min-height: 100vh;
}

.about-info {
  width: 100%;
  margin: 40px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.personal {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.personal img {
  max-width: 350px;
  width: 50%;
  margin-bottom: 25px;
}

.personal h3 {
  font-size: 1.6em;
  margin-bottom: 15px;
}

.personal p {
  width: 80%;
  font-size: 1.1em;
  line-height: 1.5em;
}
.bar {
  height: 30px;
  background: #ddd;
  z-index: 1;
  margin-bottom: 20px;
  width: 85%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.bar-title {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 15%;
  background: #cd661b;
  color: #eee;
  font-weight: bold;
  font-size: 0.8em;
}

.fill-container {
  width: 75%;
  height: 100%;
}

.percentage {
  width: 10%;
}
.bar-display {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#resume {
  background: #1b242f;
  color: #eee;
  min-height: 400px;
}

#resume .title-line {
  border-color: #eee;
}

#resume h3 {
  margin-top: 25px;
  font-size: 1.5em;
}

#resume a {
  margin-top: 25px;
  padding: 20px;
  border: 3px solid #eee;
  border-radius: 3px;
  background: transparent;
  font-size: 20px;
  color: #eee;
  text-decoration: none;
  outline: none;
  cursor: pointer;
}

#resume a:hover {
  background: #cd661b;
  border-color: #cd661b;
}
* {
  font-family: 'Raleway';
  text-align: center;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.section {
  padding: 12vh 5vw;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.section h1 {
  font-size: 2.8rem;
  text-transform: uppercase;
}

.title-line {
  margin: 20px 0 50px 0;
  border: 2px solid #444;
  border-radius: 1px;
  padding: 0 2.5rem;
}
